.container {
    display: flex;
    width: 95%;
    margin-top: 10px;
    justify-content: space-between;
    align-items: flex-start;
    .todos {
        display: flex;
        width: 47.5%;
        flex-direction: column;
        padding: 15px;
        border-radius: 5px;
        background-color: rgb(50, 195, 205);
    }
    .todosHeading {
        font-size: 30px;
        color: white;
    }
    .todosRemove {
        background-color: rgb(235, 103, 80);
    }
    .dragactive {
        background-color: rgb(0, 221, 236);
      }
      
      .dragcomplete {
        background-color: rgb(255, 38, 0);
      }
}
